*,
::before,
::after {
  padding: 0;
  box-sizing: border-box;
  margin: 0;
}

body {
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
  text-align: center;
  background-color: rgb(240, 240, 240);
}

h1 {
  margin-top: 50px;
  color: salmon;
  margin-bottom: 5px;
}

input {
  width: 300px;
  text-align: center;
  padding: 10px;
  font-size: 1rem;
  margin-bottom: 20px;
  border-radius: 10px;
  border: 1px solid black;
}

.meals-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.meal-card {
  flex: 1 0 20%;
  border: 2px solid salmon;
  border-radius: 25px;
  min-width: 180px;
  max-width: 220px;
  margin: 5px;
  padding: 10px;
}

.meal-card p {
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 9;
  overflow: hidden;
}

img {
  width: 90%;
  border-radius: 10px;
}
